<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="query">
        <el-form-item>
          <el-input v-model="query.keyword" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="search"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="pageInfo.rows" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="username" label="" width="100" sortable>
      </el-table-column>
      <el-table-column prop="password"  label="" width="150" sortable>
        <template slot-scope="scope">
          <el-input v-model="scope.row.password" auto-complete="off" show-password class="borderNone"></el-input>
        </template>
      </el-table-column>
      <el-table-column prop="email" label="" width="100" sortable>
      </el-table-column>
      <el-table-column prop="headImage" label="" min-width="50" sortable>
<!--        <el-button type="primary" @click="submitUpload"></el-button>-->
        <template #default="scope">
          <img :src="scope.row.headImage" width="40" height="40" />
        </template>
      </el-table-column>
      <el-table-column prop="age" label="" min-width="50" sortable>
      </el-table-column>
      <el-table-column prop="department.name" label="" min-width="80" sortable>
      </el-table-column>
      <el-table-column label="" >
        <template slot-scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="setRole(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-col :span="24" class="toolbar">
      <!--
      total 
      @current-change="handleCurrentChange"  handleCurrentChange()
      -->
      <el-col :span="24" class="toolbar">
        <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
        <el-pagination layout="sizes,prev, pager, next" @current-change="handleCurrentChange" @size-change="handleSizeChange"
                       :page-size="query.pageSize"
                       :page-sizes="[10, 20, 30, 40]"
                       :total="pageInfo.total"
                       :current-page="query.currentPage"
                       style="float:right;">
        </el-pagination>
      </el-col>
    </el-col>

    <!---->
    <el-dialog :title="title" :visible.sync="saveFormVisible" :close-on-click-modal="false">
      <el-form :model="saveForm" label-width="80px" :rules="saveFormRules" ref="saveForm">
        <el-form-item label="" prop="username">
          <el-input v-model="saveForm.username" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input v-model="saveForm.password" auto-complete="off" show-password></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="saveForm.email" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="headImage">
          <el-input v-model="saveForm.headImage" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="age">
          <el-input v-model="saveForm.age" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="department">
          <el-select v-model="saveForm.department.id" clearable value-key="id" placeholder="">
            <!--
              :key=""  //
              :label 
              :value    
                   value-key="id"
            -->
            <el-option
                v-for="item in department"
                :key="item.id"
                :label="item.name"
                :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="saveFormVisible = false"></el-button>
        <el-button type="primary" @click.native="saveSubmit" :loading="saveLoading"></el-button>
      </div>
    </el-dialog>
    <!--    -->
    <el-dialog title="" :visible.sync="setRoleVisible" :close-on-click-modal="false">
      <el-form :model="empRole" label-width="80px" ref="setRoleForm">
        <!--indeterminate  checkbox   indeterminatetrue,-->
        <el-checkbox :indeterminate="isIndeterminate" v-model="checkAll" @change="handleRoleCheckAllChange"></el-checkbox>
        <!---->
        <div class="checkbox-table" v-for="(p,indexkey) in role" :key="p.id">
          <template>
            <!---->
            <el-checkbox class="check1" style="font-weight: 600;margin-bottom: 15px "
                         v-model='empRole.roleId' :label="p.id"
                         @change='handleRoleCheck(1,indexkey)'>
              {{p.name}}
            </el-checkbox>

          </template>
        </div>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="setRoleVisible = false"></el-button>
        <el-button type="primary" @click.native="saveRoleMenu" :loading="saveLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>
<script>
export default {
  data() {
    return {
      title:"",
      filters: {
        name: ''
      },
      pageInfo:{
        rows: [],
        total: 0,
      },
      page: 1,
      listLoading: false,
      sels: [],//

      editFormVisible: false,//
      editLoading: false,
      editFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      saveFormVisible: false,//
      setRoleVisible:false,//
      saveLoading: false,
      isIndeterminate:false,
      checkAll:[],
      saveFormRules: {
        username: [
          { required: true, message: '', trigger: 'blur' }//blur
        ]
      },
      //
     saveForm: {
          username: '',
          password:"",
          email:"",
          headImage:"",
          age:"",
          department:{
            id:null,
            name:""
       },

      },
      empRole:{
        employeeId:null,
        roleId:[]
      },
      allRole:[],//
      role:[],
      department:[],
      employees:[],
      query:{
        // ,
        currentPage:1,  // ,
        pageSize:10,    // ,10,,,
        keyword:""
      },
      departments:[]
    }
  },
  methods: {
    search(){
      //  "",
      this.query.currentPage = 1;
      // getDepartment
      this.getEmployee();
    },
    handleRoleCheckAllChange(val){
      //valtrue  false
      this.empRole.roleId = val ? this.allRole:[];
      this.isIndeterminate = false;
    },
    //  
    handleCurrentChange(val) {
      // val,,querycurrentPage,,,currentPage
      this.query.currentPage = val;
      // getDepartment(),
      this.getEmployee();
    },
    //
    getEmployee() {
      console.log(this.query)
      this.$http.post("/employee",this.query)
          .then(result => {
            result = result.data;
            console.log(result);  // resultObj,rows() total 
            this.pageInfo = result.resultObject;
            console.log(this.pageInfo)
          })
          .catch(result => {
            console.log(result)
            this.$message({
              message: '!',
              type: 'error'
            });
          })
    },
    handleSizeChange(val){
      this.query.pageSize = val;
      this.search();
    },

    //
    getDepartments(){
      this.$http.get("/department")
          .then(result=>{
            result = result.data;
            console.log(result);
            this.department = result.resultObject;
            console.log(this.department);
      }).catch(result=>{
        console.log("fdf",result);
        this.$message({
          message:'!',
          type:'error'
        })
      })
    },

    //  index:  row:,
    handleDel: function (index, row) {
      this.$confirm('?', '', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;  // 
        // ,
        this.$http.delete("/employee/"+row.id)
            .then(result => {
              // 
              this.listLoading = false;
              // then
              result = result.data; // ,{success:true/false,message:""}
              if(result.success){
                this.$message({
                  message: '!',
                  type: 'success'
                });
                // ,,:,
                this.query.currentPage = 1;
                this.getEmployee();
              }else{
                this.$message({
                  message: '!',
                  type: 'error'
                });
              }
            })
            .catch(result => {
              this.$message({
                message: '!',
                type: 'error'
              });
            })
      }).catch(() => {

      });
    },
    //
    handleEdit: function (index, row) {
      this.saveFormVisible = true;
      this.saveForm = Object.assign({},row);

      this.getEmployee() ;
      this.getDepartments();
      // this.getDeptTree();

    },
    //
    handleAdd:function() {
      this.saveFormVisible = true;//
      this.saveForm = {
        username: '',
        password:"",
        email:"",
        headImage:"",
        age:"",
        department:{
          id:null,
          name:""
        },
      };
      this.getEmployee();
      this.getDeptTree();
    },
    setRole:function(index,row){
        //
      this.setRoleVisible = true;
      //
      this.getAllRole(row.id);
      //idempRoleroleId
      this.empRole.employeeId = row.id;
    },
    //
    handleRoleCheck(type,a = 0){
      // 
      let checkedCount = this.empRole.roleId.length;
      // ==checkAll=true
      this.checkAll = checkedCount === this.allRole.length;
      // >0<
      this.isIndeterminate = checkedCount > 0 && checkedCount < this.allRole.length;
    },
    getRoleByRoleId(roleId){
      this.$http.get("/employee/emp/"+roleId)
          .then(result => {
            console.log(result)
            result = result.data;
            if(result.success){
              this.empRole.roleId = result.resultObject;
              console.log(this.empRole.roleId);
              // 
              let checkedCount = this.empRole.roleId.length;
              // ==checkAll=true
              this.checkAll = checkedCount === this.allRole.length;
              // >0<
              this.isIndeterminate = checkedCount > 0 && checkedCount < this.allRole.length;
            }else{
              this.$message({
                message:result.message,
                type:'error'
              });
            }
          })
      .catch(error=>{
        this.$message({
          message:result.message,
          type:'error'
        });
      })
    },
    //
    saveRoleMenu(){
      this.$confirm('','',{}).then(()=>{
        this.saveLoading = true;
        this.$http.put("/employee/role",this.empRole).then(result=>{
          result = result.data;
          //
          this.saveLoading = false;
          if(result.success){
            this.$message({
              message:"",
              type:'success'
            });
            //
            this.setRoleVisible = false;
          }else{
            this.$message({
              message:result.message,
              type:'error'
            })
          }
        })
      })
    },
    //
    saveSubmit: function () {
      this.$refs.saveForm.validate((valid) => {
        if (valid) {//
          this.$confirm('', '', {}).then(() => {
            this.saveLoading = true;

            this.$http.put("/employee",this.saveForm)
            .then(result=>{
              result = result.data;
              this.saveLoading = false;
              if(result.success){
                this.$message({
                  message:'',
                  type:'success'
                });
                //
                this.saveFormVisible = false;
                //
                this.getEmployee();
              }else{
                this.$message({message:result.message,type:'error'})
              }
            }).catch(result=>{
              this.$message({
                message:'!',
                type:'error'
              })
            })

          });
        }
      });
    },
    selsChange: function (sels) {
      this.sels = sels;
    },
    //
    batchRemove: function () {
      // this.sels 
      var ids = this.sels.map(item => item.id);  // id
      this.$confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;
        // ,
        this.$http.patch("/employee",ids)
            .then(result => {
              // 
              this.listLoading = false; // 
              console.log("",result);
              result = result.data; // result.data
              console.log("",result)
              if(result.success){
                this.$message({
                  message: '!',
                  type: 'success'
                });
                // ,
                this.query.currentPage = 1;
                this.getEmployee();
              }else{
                this.$message({
                  message: '!',
                  type: 'error'
                });
              }
            })
            .catch(result => {
              this.$message({
                message: '!',
                type: 'error'
              });
            })
      }).catch(() => {

      });
    },
    getAllRole(roleId) {
      this.$http.get("/role")
      .then(result=>{
        result = result.data;
        if(result.success){
          this.role = result.resultObject;
          if(this.role && this.role.length>0){
            for(let i=0;i<this.role.length;i++){
              this.allRole.push(this.role[i].id);
            }
          }
          this.getRoleByRoleId(roleId);
        }else{
          this.$message({
            message:result.message,
            type:'error'
          })
        }
      })
    }
  },
  mounted() {
    // ,,
    this.getEmployee();
  }
}
</script>

<style scoped>

</style>


