<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="query">
        <el-form-item>
          <el-input v-model="query.keyword" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="search"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="pageInfo.rows" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="name" label="" width="100" sortable>
      </el-table-column>
      <el-table-column prop="url" label="" min-width="100" sortable>
      </el-table-column>
      <el-table-column prop="descs" label="" min-width="120" sortable>
      </el-table-column>
      <el-table-column prop="sn" label="" min-width="120" sortable>
      </el-table-column>
      <el-table-column prop="parent.name" label="" min-width="120" sortable>
      </el-table-column>
    </el-table>
    <el-col :span="24" class="toolbar">
      <!--
      total 
      @current-change="handleCurrentChange"  handleCurrentChange()
      -->
      <el-col :span="24" class="toolbar">
        <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
        <el-pagination layout="sizes,prev, pager, next" @current-change="handleCurrentChange" @size-change="handleSizeChange"
                       :page-size="query.pageSize"
                       :page-sizes="[10, 20, 30, 40]"
                       :total="pageInfo.total"
                       :current-page="query.currentPage"
                       style="float:right;">
        </el-pagination>
      </el-col>
    </el-col>
  </section>
</template>

<script>
export default {
  data() {
    return {
      title:"",
      filters: {
        name: ''
      },
      pageInfo:{
        rows: [],
        total: 0,
      },
      page: 1,
      listLoading: false,
      sels: [],//

      editFormVisible: false,//
      editLoading: false,
      editFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      saveFormVisible: false,//
      saveLoading: false,
      saveFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }//blur
        ]
      },
      //
     saveForm: {
        name: '',
        intro:"",
       manager:{
         id:null,
         username:""
       },
       parent:{
         id:null,
         name:""
       },
       state:0,
      },

      employees:[],
      query:{
        // ,
        currentPage:1,  // ,
        pageSize:10,    // ,10,,,
        keyword:""
      },
      departments:[]
    }
  },

  methods: {
    search(){
      //  "",
      this.query.currentPage = 1;
      // getDepartment
      this.getPermissions();
    },
    handleAdd:function(){

    },
    getPermissions(){
      this.$http.post("/permission",this.query)
      .then(result=>{
        result = result.data;
        console.log(result);
        this.pageInfo = result.resultObject;
        console.log(this.pageInfo)
          }).catch(result=>{
            this.$message({
              message:'!',
              type:'error'
            })
      })

    },
    //  
    handleCurrentChange(val) {
      // val,,querycurrentPage,,,currentPage
      this.query.currentPage = val;
      // getDepartment(),
      this.getPermissions();
    },
    //
    getDepartments() {
      console.log(this.query)
      this.$http.post("/department",this.query)
          .then(result => {
            result = result.data;
            console.log(result);  // resultObj,rows() total 
            this.pageInfo = result.resultObject;
            console.log(this.pageInfo)
          })
          .catch(result => {
            console.log(result)
            this.$message({
              message: '!',
              type: 'error'
            });
          })
    },
    handleSizeChange(val){
      this.query.pageSize = val;
      this.search();
    },
    //
    getEmployee(){
      this.$http.get("/employee")
          .then(result=>{
            result = result.data;
            console.log(result);
            this.employees = result.resultObject;
            console.log(this.employees);
      }).catch(result=>{
        console.log("fdf",result);
        this.$message({
          message:'!',
          type:'error'
        })
      })
    },
    selsChange: function (sels) {
      this.sels = sels;
    },


  },
  mounted() {
    // ,,
    this.getPermissions();
  }
}

</script>

<style scoped>

</style>


