<template>
  <section>
    <!---->

    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="query">
        <el-form-item>
          <el-input v-model="query.keyword" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="search"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="pageInfo.rows" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="name" label="" width="100" sortable>
      </el-table-column>
      <el-table-column prop="type" label="" min-width="120" sortable>
      </el-table-column>
      <el-table-column prop="preferentialMethodId" label="" min-width="100" sortable>
      </el-table-column>
<!--      <el-table-column prop="overMoney" label="" min-width="80" sortable>-->
<!--      </el-table-column>-->
      <el-table-column prop="description" label="" min-width="100" sortable>
      </el-table-column>
<!--      <el-table-column prop="subMoney" label="" min-width="80" sortable>-->
<!--      </el-table-column>-->
<!--      <el-table-column prop="endDate" label="" min-width="100" sortable>-->
<!--      </el-table-column>-->

<!--      <el-table-column prop="startDate" label="" min-width="100" sortable>-->
<!--      </el-table-column>-->
<!--      <el-table-column prop="creatTime" label="" min-width="100" sortable>-->
<!--      </el-table-column>-->
<!--      <el-table-column prop="creatBy" label="" min-width="100" sortable>-->
<!--      </el-table-column>-->
<!--      <el-table-column prop="editTime" label="" min-width="100" sortable>-->
<!--      </el-table-column>-->
<!--      <el-table-column prop="editBy" label="" min-width="80" sortable>-->
<!--      </el-table-column>-->
      <el-table-column label="" width="250">
        <template slot-scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-col :span="24" class="toolbar">
      <!--
      total 
      @current-change="handleCurrentChange"  handleCurrentChange()
      -->
      <el-col :span="24" class="toolbar">
        <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
        <el-pagination layout="sizes,prev, pager, next" @current-change="handleCurrentChange" @size-change="handleSizeChange"
                       :page-size="query.pageSize"
                       :page-sizes="[10, 20, 30, 40]"
                       :total="pageInfo.total"
                       :current-page="query.currentPage"
                       style="float:right;">
        </el-pagination>
      </el-col>
    </el-col>



    <!---->
    <el-dialog title="/" :visible.sync="saveFormVisible" :close-on-click-modal="false">
      <el-form :model="saveForm" label-width="80px" :rules="saveFormRules" ref="saveForm">
        <el-form-item label="">
          <el-input v-model="saveForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-radio v-model="saveForm.type" label="1"></el-radio>
          <el-radio v-model="saveForm.type" label="2"></el-radio>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
              v-model="saveForm.startDate"
              type="date"
              placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-date-picker
              v-model="saveForm.endDate"
              type="date"
              placeholder="">
          </el-date-picker>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="saveForm.preferentialMethodId" :disabled="this.saveForm.type==1" placeholder="">
            <el-option
                v-for="item in preferentialMethods"
                :key="item.id"
                :label="item.title"
                :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="saveForm.overMoney" :disabled="this.saveForm.type==1" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="saveForm.subMoney" :disabled="this.saveForm.type==1" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="saveForm.description" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="saveFormVisible = false"></el-button>
        <el-button type="primary" @click.native="saveSubmit" :loading="saveLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
export default {
  data() {
    return {
      title:"",
      filters: {
        name: ''
      },
      pageInfo:{
        rows: [],
        total: 0,
      },
      page: 1,
      listLoading: false,
      sels: [],//

      editFormVisible: false,//
      editLoading: false,
      editFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      saveFormVisible: false,//
      saveLoading: false,
      saveFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }//blur
        ]
      },
      //
      saveForm: {
        id:null,
        name:"",
        type:null,
        startDate:"",
        endDate:"",
        description:"",
        preferentialMethodId:null,
        overMoney:null,
        subMoney:null
      },
      preferentialMethods:[],
      allType:[],
      query:{
        // ,
        currentPage:1,  // ,
        pageSize:10,    // ,10,,,
        keyword:""
      },
      activity:[]
    }
  },
  methods: {
    search(){
      //  "",
      this.query.currentPage = 1;
      // getActivity
      this.getActivity();
    },
    //  
    handleCurrentChange(val) {
      // val,,querycurrentPage,,,currentPage
      this.query.currentPage = val;
      // getActivity(),
      this.getActivity();
    },
    //
    getActivity() {
      console.log(this.query)
      this.$http.post("/activity",this.query)
          .then(result => {
            result = result.data;
            console.log(result);  // resultObj,rows() total 
            this.pageInfo = result.resultObject;
            console.log(this.pageInfo)
          })
          .catch(result => {
            console.log(result)
            this.$message({
              message: '!',
              type: 'error'
            });
          })
    },

    handleSizeChange(val){
      this.query.pageSize = val;
      this.search();
    },

    //  index:  row:,
    handleDel: function (index, row) {
      this.$confirm('?', '', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;  // 
        // ,
        this.$http.delete("/activity/"+row.id)
            .then(result => {
              // 
              this.listLoading = false;
              // then
              result = result.data; // ,{success:true/false,message:""}
              if(result.success){
                this.$message({
                  message: '!',
                  type: 'success'
                });
                // ,,:,
                this.query.currentPage = 1;
                this.getActivity();
              }else{
                this.$message({
                  message: '!',
                  type: 'error'
                });
              }
            })
            .catch(result => {
              this.$message({
                message: '!',
                type: 'error'
              });
            })
      }).catch(() => {

      });
    },
    getAllactivityType() {
      console.log(this.query)
      this.$http.get("/activityType")
          .then(result => {
            result = result.data;
            console.log(result);  // resultObj,rows() total 
            this.allType = result.resultObject;
          })
          .catch(result => {
            console.log(result)
            this.$message({
              message: '!',
              type: 'error'
            });
          })
    },
    //
    handleEdit: function (index, row) {
      this.saveFormVisible = true;
      this.saveForm = Object.assign({},row);
      this.getPreferentialMethods();

    },
    //
    handleAdd:function() {
      this.saveFormVisible = true;//
      this.saveForm = {
        id:null,
        name:"",
        startDate:"",
        endDate: "",
        description: "",
        preferentialMethodId: null,
        overMoney: null,
        subMoney: null
      };

      this.getPreferentialMethods();


    },

    //
    saveSubmit: function () {
      this.$refs.saveForm.validate((valid) => {
        if (valid) {
          this.$confirm('', '', {}).then(() => {
            this.saveLoading = true;
            this.$http.put("/activity",this.saveForm)
                .then(result=>{
                  result = result.data;
                  this.saveLoading = false;
                  if(result.success){
                    this.$message({
                      message:'',
                      type:'success'
                    });
                    //
                    this.saveFormVisible = false;
                    //
                    this.getActivity();
                  }else{
                    this.$message({message:result.message,type:'error'})
                  }
                }).catch(result=>{
              this.$message({
                message:'!',
                type:'error'
              })
            })

          });
        }
      });
    },
    selsChange: function (sels) {
      this.sels = sels;
    },
    //
    batchRemove: function () {
      // this.sels 
      var ids = this.sels.map(item => item.id);  // id
      this.$confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;
        // ,
        this.$http.patch("/activity",ids)
            .then(result => {
              // 
              this.listLoading = false; // 
              console.log("",result);
              result = result.data; // result.data
              console.log("",result)
              if(result.success){
                this.$message({
                  message: '!',
                  type: 'success'
                });
                // ,
                this.query.currentPage = 1;
                this.getActivity();
              }else{
                this.$message({
                  message: '!',
                  type: 'error'
                });
              }
            })
            .catch(result => {
              this.$message({
                message: '!',
                type: 'error'
              });
            })
      }).catch(() => {

      });
    },
    getPreferentialMethods() {
        this.$http.get("/dictionaryItem/sn/activityMethod")
      .then(result=>{
        result = result.data;
        if(result.success){
          this.preferentialMethods = result.resultObject;
        }
      })
    }
  },
  mounted() {
    // ,,
    this.getActivity();
  }
}

</script>

<style scoped>

</style>


