<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="query">
        <el-form-item>
          <el-input v-model="query.keyword" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="search"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="pageInfo.rows" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="name" label="" width="100" sortable>
      </el-table-column>
      <el-table-column prop="description" label="" min-width="180" sortable>
      </el-table-column>
      <el-table-column prop="parent.name" label="" min-width="100" sortable>
      </el-table-column>
      <el-table-column label="" width="150">
        <template slot-scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-col :span="24" class="toolbar">
      <!--
      total 
      @current-change="handleCurrentChange"  handleCurrentChange()
      -->
      <el-col :span="24" class="toolbar">
        <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
        <el-pagination layout="sizes,prev, pager, next" @current-change="handleCurrentChange" @size-change="handleSizeChange"
                       :page-size="query.pageSize"
                       :page-sizes="[10, 20, 30, 40]"
                       :total="pageInfo.total"
                       :current-page="query.currentPage"
                       style="float:right;">
        </el-pagination>
      </el-col>
    </el-col>



    <!---->
    <el-dialog :title="title" :visible.sync="saveFormVisible" :close-on-click-modal="false">
      <el-form :model="saveForm" label-width="80px" :rules="saveFormRules" ref="saveForm">
        <el-form-item label="" prop="name">
          <el-input v-model="saveForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="description">
          <el-input v-model="saveForm.description" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <div class="block">
            <span class="demonstration"></span>
            <el-cascader
                v-model="saveForm.parent"
                :options="ProductType"
                :props="{ expandTrigger: 'hover',
                 checkStrictly: true,
							   label:'name',
							   value:'id'
                }"

                ></el-cascader>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="saveFormVisible = false"></el-button>
        <el-button type="primary" @click.native="saveSubmit" :loading="saveLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
export default {
  data() {
    return {
      title:"",
      filters: {
        name: ''
      },
      pageInfo:{
        rows: [],
        total: 0,
      },
      page: 1,
      listLoading: false,
      sels: [],//

      editFormVisible: false,//
      editLoading: false,
      editFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      saveFormVisible: false,//
      saveLoading: false,
      saveFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }//blur
        ]
      },
      //
     saveForm: {
        name: '',
        description:"",
        parent:{
         id:null,
         name:""
       },
      },

      employees:[],
      query:{
        // ,
        currentPage:1,  // ,
        pageSize:10,    // ,10,,,
        keyword:""
      },
      ProductType:[]
    }
  },
  methods: {
    search(){
      //  "",
      this.query.currentPage = 1;
      // getproductType
      this.getProductType();
    },
    //  
    handleCurrentChange(val) {
      // val,,querycurrentPage,,,currentPage
      this.query.currentPage = val;
      // getproductType(),
      this.getProductType();
    },
    //
    getProductType() {
      console.log(this.query)
      this.$http.post("/productType",this.query)
          .then(result => {
            result = result.data;
            console.log(result);  // resultObj,rows() total 
            this.pageInfo = result.resultObject;
            console.log(this.pageInfo)
          })
          .catch(result => {
            console.log(result)
            this.$message({
              message: '!',
              type: 'error'
            });
          })
    },
    //
    getAllProductType() {
      console.log(this.query)
      this.$http.get("/productType")
          .then(result => {
            result = result.data;
            console.log(result);  // resultObj,rows() total 
            this.pageInfo = result.resultObject;
            console.log(this.pageInfo)
          })
          .catch(result => {
            console.log(result)
            this.$message({
              message: '!',
              type: 'error'
            });
          })
    },
    handleSizeChange(val){
      this.query.pageSize = val;
      this.search();
    },

    //  index:  row:,
    handleDel: function (index, row) {
      this.$confirm('?', '', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;  // 
        // ,
        this.$http.delete("/productType/"+row.id)
            .then(result => {
              // 
              this.listLoading = false;
              // then
              result = result.data; // ,{success:true/false,message:""}
              if(result.success){
                this.$message({
                  message: '!',
                  type: 'success'
                });
                // ,,:,
                this.query.currentPage = 1;
                this.getProductType();
              }else{
                this.$message({
                  message: '!',
                  type: 'error'
                });
              }
            })
            .catch(result => {
              this.$message({
                message: '!',
                type: 'error'
              });
            })
      }).catch(() => {

      });
    },
    //
    handleEdit: function (index, row) {
      this.saveFormVisible = true;
      this.saveForm = Object.assign({},row);
      if(!this.saveForm){//
        this.saveForm={
          id:null
        }
      }
      this.getProductType();
      this.getAllProductType();

    },
    //
    handleAdd:function() {
      this.saveFormVisible = true;//
      this.saveForm = {
        name: '',
        intro: "",
        manager: {
          id: null
        },
        parent: {
          id: null
        },
        state: null
      };
      this.getProductType();

    },

    //
    saveSubmit: function () {
      this.$refs.saveForm.validate((valid) => {
        if (valid) {
          this.$confirm('', '', {}).then(() => {
            this.saveLoading = true;
            //id
            //saveFormparent
            var parent = this.saveForm.parent;
            if(parent && parent.length>0){//
                this.saveForm.parent = {id:parent[[parent.length-1]]};
            }else{
              this.saveForm.parent = {id:null};
            }
            console.log(this.saveForm);
            this.$http.put("/productType",this.saveForm)
            .then(result=>{
              result = result.data;
              this.saveLoading = false;
              if(result.success){
                this.$message({
                  message:'',
                  type:'success'
                });
                //
                this.saveFormVisible = false;
                //
                this.getProductType();
              }else{
                this.$message({message:result.message,type:'error'})
              }
            }).catch(result=>{
              this.$message({
                message:'!',
                type:'error'
              })
            })

          });
        }
      });
    },
    selsChange: function (sels) {
      this.sels = sels;
    },
    //
    batchRemove: function () {
      // this.sels 
      var ids = this.sels.map(item => item.id);  // id
      this.$confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;
        // ,
        this.$http.patch("/productType",ids)
            .then(result => {
              // 
              this.listLoading = false; // 
              console.log("",result);
              result = result.data; // result.data
              console.log("",result)
              if(result.success){
                this.$message({
                  message: '!',
                  type: 'success'
                });
                // ,
                this.query.currentPage = 1;
                this.getProductType();
              }else{
                this.$message({
                  message: '!',
                  type: 'error'
                });
              }
            })
            .catch(result => {
              this.$message({
                message: '!',
                type: 'error'
              });
            })
      }).catch(() => {

      });
    }
  },
  mounted() {
    // ,,
    this.getProductType();
  }
}

</script>

<style scoped>

</style>


