<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="query">
        <el-form-item>
          <el-input v-model="query.keyword" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="search"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
      </el-form>
    </el-col>

    <!---->
    <el-table :data="pageInfo.rows" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="name" label="" sortable>
      </el-table-column>
      <el-table-column prop="url" label="" sortable>
      </el-table-column>
      <el-table-column prop="icon" label="" sortable>
      </el-table-column>
      <el-table-column prop="parent.name" label="" sortable>
      </el-table-column>
      <el-table-column label="">
        <template scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="handleDel(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <!---->
    <el-col :span="24" class="toolbar">
      <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
      <el-pagination
          layout="prev, pager, next"
          @current-change="handleCurrentChange"
          :page-size="query.pageSize"
          :total="pageInfo.total" style="float:right;">
      </el-pagination>
    </el-col>

    <el-dialog title="/" :visible.sync="saveFormVisible" :close-on-click-modal="false">
      <el-form :model="saveForm" label-width="80px" :rules="saveFormRules" ref="saveForm">
        <!--prop="name" saveFormRules name-->
        <el-form-item label="" prop="name">
          <el-input v-model="saveForm.name" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="url">
          <el-input v-model="saveForm.url" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="icon">
          <el-input v-model="saveForm.icon" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" >
          <div class="block">
            <span class="demonstration"></span>
            <el-cascader
                v-model="saveForm.parent"
                :options="menus"
                :props="{ expandTrigger: 'hover',
                 checkStrictly: true,
							   label:'name',
							   value:'id'
                }"

            ></el-cascader>
          </div>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="saveFormVisible = false"></el-button>
        <el-button type="primary" @click.native="saveSubmit" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

