    handleAdd(){
      //  
      this.saveFormVisible = true;
      // =,
      this.saveForm = {
        id:null,
        name:"",
        sn:""
      }
    },
    handleEdit(index,row){
      //  
      this.saveFormVisible = true;
      //   
      this.saveForm = Object.assign({},row);
      this.saveForm.parent = row.parent.id
      this.getMenu();
      this.getMenuTree();

    },
    getMenuTree(){
      this.$http.get("menu/tree").then(result=>{
        result = result.data;
        console.log(result);
        this.menus = result.resultObject;//
        console.log(this.menus);
      }).catch(result=>{
        console.log(result);
        this.$message({
          message:'',
          type:'error'
        })
      })
    },
    batchRemove: function () {
      this.$confirm('?', '', {
        type: 'warning'
      }).then(() => {
        this.saveLoading = true;  // 
        // id
        let ids = this.sels.map(x => x.id);

        this.$http.patch("/menu",ids)
            .then(result => {
              result = result.data;
              // result {success:true,message:""}
              if(result.success){
                // 
                this.$message({
                  message: "",
                  type: 'success'
                });
                // 
                this.saveLoading = false;
                // 
                this.search();
              }
            })
      }).catch(() => {

      });
    },
    selsChange(val){
      // val 
      this.sels = val;
    },
    handleDel: function (index, row) {
      this.$confirm('?', '', {
        type: 'warning'
      }).then(() => {
        this.saveLoading = true;  // 
        this.$http.delete("/menu/"+row.id)
            .then(result => {
              result = result.data;
              if(result.success){
                // 
                this.$message({
                  message: "",
                  type: 'success'
                });
                // 
                this.saveLoading = false;
                // 
                this.search();
              }
            })
      }).catch(() => {

      });
    },
    search(){
      // 1
      this.query.currentPage = 1;
      this.getMenu();
    },
    handleCurrentChange(val) {
      // val 
      this.query.currentPage = val;
      this.getMenu();
    },
    //
    getMenu() {
      this.$http.post("/menu",this.query)
          .then(result => {
            result = result.data;
            console.log(result);
            if(result.success){ // 
              this.pageInfo = result.resultObject;
            }else{
              this.$message({
                message: result.message,
                type: 'error'
              });
            }
          })
          .catch(result => {
            this.$message({
              message: '!',
              type: 'error'
            });
          })
    },
  },
  mounted() {
    // ,
    this.getMenu();
