//     sessionStorage.removeItem('user');
//   }
//   let user = JSON.parse(sessionStorage.getItem('user'));
//   if (!user && to.path !== '/login') {
//     next({ path: '/login' })
//   } else {
//     next()
//   }
// })

//router.afterEach(transition => {
//NProgress.done();
//});
// axios:token
// ,axios,axios,axios
axios.interceptors.request.use(
    config => {
      // localStoragetoken
      let token = localStorage.getItem("token");

      // token,
      if (token) {
        config.headers.token = token;
      }
      return config
    },
    error => {
      return Promise.reject(error+'')
    })
// :,
axios.interceptors.response.use(function(response){
  //
  let result = response.data;  // response.data   {success:false,message:"noLogin"}


    // result=JSON.parse(result)
  if(!result.success && result.message === "noLogin"){ // ,,
      // alert("!")
      response.data.message="Please login!!!"
    router.push({ path: '/login' });  // ,
      Message({message:"Please login!!!",type:"error"})
    return Promise.resolve(response);
  }else if(!result.success && result.message == "noPermission"){
      router.push({path:'/403'});
      return response
  }else{
    // ,
    return response;
  }
},function(err){
  return Promise.reject(err)
})


new Vue({
  //el: '#app',
  //template: '<App/>',
  router,
  store,
  //components: { App }
  render: h => h(App)
}).$mount('#app')




