import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import Page4 from './views/nav2/Page4.vue'
import Page5 from './views/nav2/Page5.vue'
import Page6 from './views/nav3/Page6.vue'
import echarts from './views/charts/echarts.vue'

//========================
import noPermission from './views/403.vue'
import Department from "./views/org/Department";
import Permission from "./views/auth/Permission";
import Role from "./views/auth/Role";
import Employee from "./views/org/Employee";
import Menu from "./views/auth/Menu";
import config from "./views/sys/config";
import dictionary from "./views/sys/dictionary";
import dictionaryItem from "./views/sys/dictionaryItem";
import ProductType from "./views/prod/ProductType";
import Product from "./views/prod/Product";
import BusinessActivity from "./views/mkt/BusinessActivity";
import Clue from "./views/mkt/Clue";
import Order from "./views/order/Order";
import Contract from "./views/order/Contract";
import Business from "./views/mkt/Business";
let routes = [
    {
        path: '/login',
        component: Login,
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-bar-chart',
        leaf: true, // ,:,
        children: [
            { path: '/echarts', component: echarts, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/department', component: Department, name: ''},
            { path: '/employee', component: Employee, name: '' },

        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/permission', component: Permission, name: ''},
            { path: '/menu', component: Menu, name: '' },
            { path: '/role', component: Role, name: ''},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/config', component: config, name: ''},
            { path: '/dictionary', component: dictionary, name: ''},
            { path: '/dictionaryItem', component: dictionaryItem, name: ''},

        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'el-icon-message',//class
        children: [
            { path: '/productType', component: ProductType, name: '' },
            { path: '/product', component: Product, name: '' },
            { path: '/form', component: Form, name: 'Form' },
            { path: '/user', component: user, name: '' },
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/businessActivity', component: BusinessActivity, name: '' },
            { path: '/clue', component: Clue, name: '' },
            {path:'/business',component: Business,name:''},
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-id-card-o',
        children: [
            { path: '/order', component: Order, name: '' },
            { path: '/contract', component: Contract, name: '' }
        ]
    },
    {
        path: '/',
        component: Home,
        name: '',
        iconCls: 'fa fa-address-card',
        leaf: true,//
        children: [
            { path: '/page6', component: Page6, name: '' }
        ]
    },

    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    },
    {
        path: '/',
        component: Home,
        name: '',
        hidden: true,
        children: [
            { path: '/403', component: noPermission, name: '' }
        ]
    }
];

export default routes;


