<template>
  <section>
    <!---->
    <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
      <el-form :inline="true" :model="query">
        <el-form-item>
          <el-input v-model="query.keyword" placeholder=""></el-input>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" v-on:click="search"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleAdd"></el-button>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" @click="handleBatchAssign" :disabled="this.sels.length == 0"></el-button>
        </el-form-item>
      </el-form>
    </el-col>
    <el-table :data="pageInfo.rows" highlight-current-row v-loading="listLoading" @selection-change="selsChange" style="width: 100%;">
      <el-table-column type="selection" width="55">
      </el-table-column>
      <el-table-column type="index" width="60">
      </el-table-column>
      <el-table-column prop="fullName" label="" width="100" sortable>
      </el-table-column>
      <el-table-column prop="appellation" label="" width="100" sortable>
      </el-table-column>
      <el-table-column prop="company" label="" width="100" sortable>
      </el-table-column>
      <el-table-column prop="email" label="" width="100" sortable>
      </el-table-column>
      <el-table-column prop="phone" label="" width="100" sortable>
      </el-table-column>
      <el-table-column prop="state" label=""  width="100" sortable>
        <template slot-scope="scope">
          <span v-if="scope.row.state==0" style="color:grey"></span>
          <span v-if="scope.row.state==1" style="color:skyblue"></span>
          <span v-if="scope.row.state==3" style="color:lawngreen"></span>
          <span v-if="scope.row.state==2" style="color:lawngreen"></span>

        </template>
      </el-table-column>
      <el-table-column prop="description" label="" width="100" sortable>
      </el-table-column>
      <el-table-column label="" >
        <template slot-scope="scope">
          <el-button size="small" @click="handleEdit(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="handleClueActivity(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" v-if="scope.row.state == 2" @click="handleDel(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" @click="handleBusiness(scope.$index, scope.row)"></el-button>
          <el-button type="danger" size="small" v-if ="scope.row.state==1" @click="handleClueActivity(scope.$index, scope.row)" ></el-button>
          <el-button type="danger" v-if="scope.row.state == 2 || scope.row.state == 3" size="small" @click="handleClueActivity(scope.$index, scope.row)"></el-button>
        </template>
      </el-table-column>
    </el-table>

    <el-col :span="24" class="toolbar">
      <!--
      total 
      @current-change="handleCurrentChange"  handleCurrentChange()
      -->
      <el-col :span="24" class="toolbar">
        <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0"></el-button>
        <el-pagination layout="sizes,prev, pager, next" @current-change="handleCurrentChange" @size-change="handleSizeChange"
                       :page-size="query.pageSize"
                       :page-sizes="[10, 20, 30, 40]"
                       :total="pageInfo.total"
                       :current-page="query.currentPage"
                       style="float:right;">
        </el-pagination>
      </el-col>
    </el-col>

    <!---->

    <el-dialog :title="title" :visible.sync="saveFormVisible" :close-on-click-modal="false">
      <el-form :model="saveForm" label-width="80px" :rules="saveFormRules" ref="saveForm">
        <el-form-item label="" prop="fullName">
          <el-input v-model="saveForm.fullName" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="appellation">
          <el-input v-model="saveForm.appellation" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="company">
          <el-input v-model="saveForm.description" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="saveForm.email" auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="" prop="phone">
          <el-input v-model="saveForm.phone" auto-complete="off"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="saveFormVisible = false"></el-button>
        <el-button type="primary" @click.native="saveSubmit" :loading="saveLoading"></el-button>
      </div>
    </el-dialog>
<!--    -->
    <el-dialog title="" :visible.sync="clueActivityVisible" :close-on-click-modal="false">
      <el-form :model="clueActivity" label-width="80px" :rules="saveFormRules" ref="saveForm">
        <el-form-item label="">
          <el-input v-model="clueActivity.clueName" disabled auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="clueActivity.activityIds" multiple placeholder="">
            <el-option
                v-for="item in activities"
                :key="item.id"
                :label="item.name"
                :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="saveFormVisible = false"></el-button>
        <el-button type="primary" @click.native="saveClueActivity" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
<!--    -->
    <el-dialog title="" :visible.sync="businessVisible" :close-on-click-modal="false">
      <el-form :model="business" label-width="80px" ref="businessForm">
        <el-form-item label="">
          <el-input v-model="business.fullName" disabled auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-input v-model="business.phone" disabled auto-complete="off"></el-input>
        </el-form-item>
        <el-form-item label="">
          <el-select v-model="business.productId" placeholder="">
            <el-option
                v-for="item in products"
                :key="item.id"
                :label="item.name"
                :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="businessVisible = false"></el-button>
        <el-button type="primary" @click.native="saveBusiness" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
<!--    -->
    <el-dialog title="" :visible.sync="batchAssignVisible" :close-on-click-modal="false">
      <el-form :model="batchAssign" label-width="80px" ref="batchAssginForm">
        <el-form-item label="">
          <el-select v-model="batchAssign.owner" placeholder="">
            <el-option
                v-for="item in employees"
                :key="item.id"
                :label="item.username"
                :value="item.id">
            </el-option>
          </el-select>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click.native="batchAssignVisible = false"></el-button>
        <el-button type="primary" @click.native="saveBatchAssign" :loading="addLoading"></el-button>
      </div>
    </el-dialog>
  </section>
</template>

<script>
export default {
  data() {
    return {
      title:"",
      filters: {
        name: ''
      },
      pageInfo:{
        rows: [],
        total: 0,
      },
      page: 1,
      listLoading: false,
      sels: [],//

      editFormVisible: false,//
      editLoading: false,
      editFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }
        ]
      },
      saveFormVisible: false,//
      saveLoading: false,
      saveFormRules: {
        name: [
          { required: true, message: '', trigger: 'blur' }//blur
        ]
      },
      //
      saveForm: {
        fullName: '',
        appellation:"",
        company:"",
        email:"",
        phone:"",

      },
      addLoading:false,    // ,
      clueTypes:[],
      allType:[],
      query:{
        // ,
        currentPage:1,  // ,
        pageSize:10,    // ,10,,,
        keyword:""
      },
      clue:[],
     batchAssignVisible:false,
         employees:[],
        batchAssign:{
          owner:null,
          state:null,// 
    },
      clueActivityVisible:false,
      clueActivity:{
        clueId:null,
        activityIds:[],
        clueName:""
      },
      activities:[],
    businessVisible:false,
        products:[],
        business:{
         clueId:null,
          fullName:"",
          phone:"",
          productId:null
    }
    }

  },
  methods: {
    search(){
      //  "",
      this.query.currentPage = 1;
      // getProduct
      this.getClue();
    },
    saveBusiness(){
      this.$http.post("/clue/business",this.business)
      .then(result=>{
        result = result.data;
        if(result.success){
          this.$message({
            message:result.message,
            type:'success',
          });
          //
          this.businessVisible = false;
          //
          this.getClue();
        }else{
          this.$message({
            message:result.message,
            type:'error'
          });
        }
      })
    },
    //  
    handleCurrentChange(val) {
      // val,,querycurrentPage,,,currentPage
      this.query.currentPage = val;
      // getProduct(),
      this.getClue();
    },
    //
    getClue() {
      console.log(this.query)
      this.$http.post("/clue",this.query)
          .then(result => {
            result = result.data;
            console.log(result);  // resultObj,rows() total 
            this.pageInfo = result.resultObject;
            console.log(this.pageInfo)
          })
          .catch(result => {
            console.log(result)
            this.$message({
              message: '!',
              type: 'error'
            });
          })
    },


    handleBatchAssign(){
      //
        this.batchAssignVisible = true;
        this.getAllEmployee();
    },
    saveBatchAssign(){
      // id
      let ids = this.sels.map(x => x.id);
      this.batchAssign.clueIds = ids;
      // id
      this.$http.put("/clue/batch/assign",this.batchAssign)
          .then(result => {
            result = result.data;
            if(result.success){
              this.$message({
                message: result.message,
                type: 'success'
              });
              // 
              this.batchAssignVisible = false;
              // 
              this.getClue();
            }else{
              this.$message({
                message: result.message,
                type: 'error'
              });
            }
          })
    },
    handleSizeChange(val){
      this.query.pageSize = val;
      this.search();
    },
    handleClueActivity(index,row){
      // 
      this.clueActivityVisible = true;
      this.clueActivity.clueName = row.fullName;
      this.clueActivity.clueId = row.id;
      // 
      this.$http.get("/activity/type/1")
          .then(result => {
            result = result.data;
            if(result.success){
              this.activities = result.resultObject;
            }
          })
    },

    handleBusiness(index,row){
      // 
      this.businessVisible = true;
      // ,products
      this.getAllProducts();
      // business,
      this.business.clueId = row.id;
      this.business.fullName = row.fullName;
      this.business.phone = row.phone;
    },
    getAllProducts(){
      this.$http.get("/product")
          .then(result => {
            result = result.data;
            console.log(result)
            // 
            this.products = result.resultObject;
          })
    },
    //  index:  row:,
    handleDel: function (index, row) {
      this.$confirm('?', '', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;  // 
        // ,
        this.$http.delete("/clue/"+row.id)
            .then(result => {
              // 
              this.listLoading = false;
              // then
              result = result.data; // ,{success:true/false,message:""}
              if(result.success){
                this.$message({
                  message: '!',
                  type: 'success'
                });
                // ,,:,
                this.query.currentPage = 1;
                this.getClue();
              }else{
                this.$message({
                  message: '!',
                  type: 'error'
                });
              }
            })
            .catch(result => {
              this.$message({
                message: '!',
                type: 'error'
              });
            })
      }).catch(() => {

      });
    },

    //
    handleEdit: function (index, row) {
      this.saveFormVisible = true;
      this.saveForm = Object.assign({},row);
      this.getClue();
      this.getAllProductType();

    },
    //
    handleAdd:function() {
      this.saveFormVisible = true;//
      this.saveForm = {
        name: '',
        price:null,
        description:"",
        resource:"",
        type:{
          id:null,
          name:""
        },
      };
      this.getClue();
      this.getAllProductType();

    },
    saveClueActivity(){
      this.$http.put("/clue/activity",this.clueActivity)
          .then(result => {
            result = result.data;
            if(result.success){
              // 
              this.clueActivityVisible = false;
              // 
              this.$message({
                message: "",
                type: 'success'
              });
            }else{
              this.$message({
                message: result.message,
                type: 'error'
              });
            }
          })
    },
    //
    saveSubmit: function () {
      this.$refs.saveForm.validate((valid) => {
        if (valid) {
          this.$confirm('', '', {}).then(() => {
            this.saveLoading = true;
            //id
            //saveFormparent
            var parent = this.saveForm.parent;
            if(parent && parent.length>0){//
              this.saveForm.parent = {id:parent[[parent.length-1]]};
            }else{
              this.saveForm.parent = {id:null};
            }
            console.log(this.saveForm);
            this.$http.put("/clue",this.saveForm)
                .then(result=>{
                  result = result.data;
                  this.saveLoading = false;
                  if(result.success){
                    this.$message({
                      message:'',
                      type:'success'
                    });
                    //
                    this.saveFormVisible = false;
                    //
                    this.getClue();
                  }else{
                    this.$message({message:result.message,type:'error'})
                  }
                }).catch(result=>{
              this.$message({
                message:'!',
                type:'error'
              })
            })

          });
        }
      });
    },
    selsChange: function (sels) {
      this.sels = sels;
    },
    //
    batchRemove: function () {
      // this.sels 
      var ids = this.sels.map(item => item.id);  // id
      this.$confirm('', '', {
        type: 'warning'
      }).then(() => {
        this.listLoading = true;
        // ,
        this.$http.patch("/clue",ids)
            .then(result => {
              // 
              this.listLoading = false; // 
              console.log("",result);
              result = result.data; // result.data
              console.log("",result)
              if(result.success){
                this.$message({
                  message: '!',
                  type: 'success'
                });
                // ,
                this.query.currentPage = 1;
                this.getClue();
              }else{
                this.$message({
                  message: '!',
                  type: 'error'
                });
              }
            })
            .catch(result => {
              this.$message({
                message: '!',
                type: 'error'
              });
            })
      }).catch(() => {

      });
    },
    getAllEmployee() {
      this.$http.get("/employee")
          .then(result => {
            result = result.data;
            console.log(result)
            // 
            this.employees = result.resultObject;
          })
    }
  },
  mounted() {
    // ,,
    this.getClue();
  }
}

</script>

<style scoped>

</style>


